.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: var(--background-color);
}

h2 {
  color: var(--text-color);
  margin-bottom: 1em;
}

.form-group {
  margin-bottom: 1em;
  width: 100%;
  max-width: 300px; /* Maximale Breite der Eingabefelder */
}

label {
  display: block;
  margin-bottom: 0.5em;
  color: var(--text-color);
}

input {
  width: 100%;
  padding: 0.5em;
  border: 1px solid var(--input-border-color);
  border-radius: 4px;
}

button {
  padding: 0.5em 1em;
  margin: 0.5em 0;
  border: none;
  border-radius: 4px;
  background-color: var(--primary-color);
  color: white;
  font-size: 1em;
  cursor: pointer;
  
}

button:hover {
  background-color: var(--primary-hover-color);
}


