:root {
    --primary-color: #3498db;
    --primary-hover-color: #2980b9;
    --background-color: #f5f5f5;
    --text-color: #333;
    --input-border-color: #ccc;
  }
  


/*Taskleiste*/
nav {
    background-color: var(--primary-color);
    padding: 1em;
  }
  
  ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
  }
  
  li {
    margin: 0 1em;
  }
  
  a {
    color: white;
    text-decoration: none;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  