  .main-container {
    padding: 2em;
    background-color: var(--background-color);
  }
  
  h2 {
    color: var(--text-color);
    margin-bottom: 1em;
  }
  
  .posts-container {
    display: flex;
    flex-direction: column;
    gap: 1em;
  }
  
  .post {
    background: var(--form-background);
    padding: 1em;
    border-radius: 4px;
    box-shadow: 0 0 5px var(--box-shadow-color);
  }
  