  .login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: var(--background-color);
  }
  
  .login-form, .register-form {
    background: var(--form-background);
    padding: 2em;
    border-radius: 8px;
    box-shadow: 0 0 10px var(--box-shadow-color);
    width: 300px;
  }
  
  .form-group {
    margin-bottom: 1em;
  }
  
  label {
    display: block;
    margin-bottom: 0.5em;
    color: var(--text-color);
  }
  
  input {
    width: 100%;
    padding: 0.5em;
    border: 1px solid var(--input-border-color);
    border-radius: 4px;
  }
  
  button {
    width: 100%;
    padding: 0.75em;
    border: none;
    border-radius: 4px;
    background-color: var(--primary-color);
    color: white;
    font-size: 1em;
    cursor: pointer;
  }
  
  button:hover {
    background-color: var(--primary-hover-color);
  }
  